import React, { Component } from 'react'

import Button from 'components/Button'
import Chart from 'chart.js'

export default class Download extends Component {
	constructor() {
		super()

		this.state = {
			chartData: null
		}
	}

	componentDidMount() {
		if(this.props.data) {
			let data = [];

			for(let key in this.props.data) {
				data = [...data, this.props.data[key]];
			}

			let chartData = {
				labels: Object.keys(this.props.data),
				datasets: [
					{
						label: "# of Records Parsed",
						backgroundColor: 'rgba(255, 255, 255, 0.3)',
						borderColor: 'rgba(255, 255, 255, 0.6)',
						data
					}
				]
			}

			let ctx = document.getElementById("myChart");

			let myChart = new Chart(ctx, {
				type: 'bar',
				data: chartData,
				options: {
					responsive: true
				}
			});

			Chart.defaults.global.defaultFontColor = '#FFFFFF'
			Chart.defaults.global.defaultFontFamily = `-apple-system, BlinkMacSystemFont,
			    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
			    "Fira Sans", "Droid Sans", "Helvetica Neue",
			    sans-serif`
		}
	}

	render() {
		return (
			<div className="row">
				<div className="col-xs-12">
					{window.navigator.msSaveBlob ?
						<Button
							href={this.props.file.blobURL}
							download={this.props.file.newFileName}
							type="ieDownload"
							onClick={() => this.ieDownload()}
						>
							Download
						</Button> :
						<Button
							href={this.props.file.blobURL}
							download={this.props.file.newFileName}
							type="download"
						>
							Download
						</Button>}
				</div>
				{this.props.data !== null ?
					<div className="col-xs-12">
						<div className="break" />
						<h4 style={{marginBottom: '10px'}}>Statistics</h4>
						<div style={{marginBottom: '20px'}}>
							Below is a bar chart that shows the results of the multi-value column parsing process. The x-axis shows
							how many separate entries were matched in a given record, and the y-axis shows how many records were
							split by the number in the corresponding x-axis.
						</div>

						<canvas id="myChart" />
					</div>: null}

			</div>
		)
	}

	ieDownload() {
		window.navigator.msSaveBlob(this.props.file.blob, this.props.newFileName);
	}
}



