import React, { Component } from 'react'

// Helpers
import axios from 'axios'

// Redux
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { updateStateStore } from 'modules/state'
import { updateFileStore } from 'modules/file'

// Custom React components
import Button from 'components/Button'

// Third-party React components
import {
	Step,
	Stepper,
	StepLabel,
	StepContent
} from 'material-ui/Stepper'
import FaAngleLeft from 'react-icons/lib/fa/angle-left'


function mapStateToProps({ state, file, functions }) {
	return { state, file, functions }
}

function mapDispatchToProps(dispatch) {
	return bindActionCreators({
		updateStateStore,
		updateFileStore
	}, dispatch);
}

@connect(mapStateToProps, mapDispatchToProps)
export default class CustomStepper extends Component {
	constructor(props) {
		super(props)
	}

	render() {
		return (
			<div className="stepper">
				<Stepper
					activeStep={this.props.stepIndex}
					orientation="vertical"
				>
					<Step>
						<StepLabel>Upload</StepLabel>
						<StepContent>
							Select where each record will be split in your file and then upload the file via
							clicking the upload form or drag'n'dropping your file onto the dropzone on the right.
							<Button
								onClick={() => this.openDropzone()}>
								Upload
							</Button>
						</StepContent>
					</Step>
					<Step>
						<StepLabel>Entry Split Configuration</StepLabel>
						<StepContent>
							The default <a href="https://en.wikipedia.org/wiki/Regular_expression" target="_blank">Regular Expression</a> provided by AxonAI used to
							determine where each entry ends and a new begins. You will find an input field for the RegExp
							value and a preview of the first record from your uploaded file. Any matched string values
							in the record preview will be highlighted in a white box and deemed a separate entry. Continue once you have correctly identified
							all valid entries.
							<div>
								<Button
									onClick={() => this.goBack('upload')}
									style={{padding: '10px', marginRight: '10px', display: 'inline-block'}}
								>
									<FaAngleLeft />
								</Button>
								<Button
									onClick={() => this.props.functions.identifyEntries()}
									style={{display: 'inline-block'}}
								>
									Continue
								</Button>
							</div>
						</StepContent>
					</Step>
					<Step>
						<StepLabel>Determine if a Multi-Value Column Exists</StepLabel>
						<StepContent>
							Shown are the first eight records from your uploaded file. If one column has multiple values
							that need to be split and broken into multiple records, please check the box next to the column title
							and proceed onto the next step to configure the rules applied to this column.
							<div>
								<Button
									onClick={() => this.goBack('entrySplit')}
									style={{padding: '10px', marginRight: '10px', display: 'inline-block'}}
								>
									<FaAngleLeft />
								</Button>
								<Button
									onClick={this.multiValueOptions}
									style={{display: 'inline-block'}}
								>
									Continue
								</Button>
							</div>
						</StepContent>
					</Step>
					<Step>
						<StepLabel>Customize Multi-Value Column Parsing Rules</StepLabel>
						<StepContent>
							Shown are all of the default rules that are applied to multi-value columns.
							If you would like to disable a rule, click on the checkbox to the left of the title.
							You can edit the specifics of a rule by clicking on the down-facing caret located
							to the right of the title. You must create a new profile to add custom rules. Clicking the
							button with the <strong>eye</strong> will preview your active profile's rules on the first eight entries within
							the multi-value column you chose in the step prior.

							<div>
								<Button
									onClick={() => this.goBack('setMultiValue')}
									style={{padding: '10px', marginRight: '10px', display: 'inline-block'}}
								>
									<FaAngleLeft />
								</Button>
								<Button
									onClick={() => this.props.splitMultiValue()}
									style={{display: 'inline-block'}}
								>
									Continue
								</Button>
							</div>
						</StepContent>
					</Step>
					<Step>
						<StepLabel>Download Parsed File</StepLabel>
						<StepContent>
							All done! From here, you can download your parsed file and also start the process over again.
							Thanks for using AXON: Parse!

							<Button
								onClick={this.resetWizard}
							>
								Upload More
							</Button>
						</StepContent>
					</Step>
				</Stepper>
			</div>
		)
	}

	openDropzone = () => {
		if(this.props.file.dropzoneRef) {
			this.props.file.dropzoneRef.open();
		}
	}

	goBack = (state) => {
		let amount = -14.28;

		if(state === 'upload') amount = -28.56

		this.props.increaseProgress(amount);
		setTimeout(() => this.props.updateStateStore({ route: state }), 200);
	}

	multiValueOptions = () => {
		this.props.increaseProgress(14.28);

		if(this.props.activeMultiValue !== null) {
			setTimeout(() => this.props.updateStateStore({ route: 'rules' }), 200);
		} else {
			this.props.updateStateStore({ loading: true });
			axios.post('/createOutputRecords').then(res => {
				console.log(`[AXONPARSE] Output records created: ${res.data.recordsCnt}`);
				this.props.increaseProgress(14.28);

				const parsedPath = `${this.props.originalPath}_PARSED`;
				axios.post('/exportRecords', {
					filePathOut: parsedPath
				}).then(res => {
					console.log(`[AXONPARSE] Records exported: ${res.data.recordsCnt}`);
					this.props.increaseProgress(14.28);
					// Set parsed path on state to be downloaded
					axios.post('/download', { filePath: parsedPath }).then(res => {
						const blob = new Blob([res.data]);
						this.props.updateStateStore({
							route: 'download',
							loading: false
						});

						this.props.increaseProgress(14.28);

						this.props.updateFileStore({ blobURL: URL.createObjectURL(blob), blob });
					}).catch(err => console.error(err));
				}).catch(err => console.error(err));
			}).catch(err => console.error(err));
		}
	}

	resetWizard = () => {
		this.props.updateStateStore({
			progress: 0,
			route: 'upload'
		});

		this.props.updateMultiValueStore({ multiValueIndex: null, multiValueCountData: null });
	}
}



