import React, { Component } from 'react'

// Helpers
import axios from 'axios'
import { find } from 'lodash'
import XRegExp from 'xregexp'

// Custom React components
import Button from 'components/Button'
import RuleProfiles from 'components/Rules/RuleProfiles'

// Third-party React components
import FaCaretDown from 'react-icons/lib/fa/caret-down'
import FaCaretUp from 'react-icons/lib/fa/caret-up'
import FaAngleLeft from 'react-icons/lib/fa/angle-left'
import { Tabs, Tab } from 'material-ui/Tabs'
import Select from 'react-select'


export default class Rules extends Component {
	constructor(props) {
		super(props)

		this.state = {
			rulePreview: false
		}
	}

	render() {
		return (
			<div className="multiValueRules">
				{this.renderPreview()}
				<div className="break" />
				<RuleProfiles
					activeRuleProfile={this.props.multiValue.activeRuleProfile}
					ruleProfiles={this.props.multiValue.ruleProfiles}
					expandedRules={this.props.multiValue.expandedRules}
					updateMultiValueStore={this.props.updateMultiValueStore}
					addNewRuleProfile={this.props.addNewRuleProfile}
					previewRules={this.previewRules}
					rulePreview={this.state.rulePreview}
					addNewRule={this.props.addNewRule}
					deleteRule={this.props.deleteRule}
					moveRule={this.props.moveRule}
				/>
				<div className="break" />
				{this.renderExpansionIdentifier()}
			</div>
		)
	}

	componentDidMount() {
		this.props.updateFunctionsStore({ splitMultiValue: this.splitMultiValue });
	}

	renderExpansionIdentifier = () => {
		let content;

		if(!this.props.multiValue.prependRowExpansionIdentifier) {
			content = (
				<Button
					onClick={() => this.props.updateMultiValueStore({ prependRowExpansionIdentifier: true })}
				>
					Add Identifier Column
				</Button>
			)
		} else {
			content = (
			<div>
				<Button
					onClick={() => this.props.updateMultiValueStore({ prependRowExpansionIdentifier: false })}
					style={{ padding: '10px', fontSize: '14px', marginBottom: '20px' }}
				>
					Remove
				</Button><br />
				<label htmlFor="header">Header</label><br />
				<input
					className="password"
					value={this.props.multiValue.prependRowExpansionIdentifierHeader}
					onChange={e => this.editIdentifierHeader(e)}
				/>
			</div>
			)
		}

		const element = (
		<div className="expansion-identifier">
			<h5 style={{marginBottom: '10px'}}>Expansion Identifier</h5>
			When these rules are applied to the multi-value column, any values that are split from the original will
			become a new row. Would you like to add an identifier to know when this type of row appears in your
			parsed data file?
			<br />
			<br />
			{content}
		</div>
		)

		return element;
	}

	editIdentifierHeader = (e) => {
		this.props.updateMultiValueStore({ prependRowExpansionIdentifierHeader: e.target.value });
	}

	renderPreview = () => {
		const {
			ruleProfiles,
			activeRuleProfile
		} = this.props.multiValue;

		let tabs = [];

		for(let i=0;i<this.props.table.data.length;i++) {
			let parseStr = this.props.table.data[i][this.props.multiValue.multiValueIndex];

			parseStr = parseStr.replace(/"/g, '');

			if(this.state.rulePreview) {
				for(let i=0;i<ruleProfiles[activeRuleProfile].rules.length;i++) {
					var rule=ruleProfiles[activeRuleProfile].rules[i];
					if(rule.apply)
					{
						var exp = XRegExp(rule.regExp,rule.regExpApplication);
						if(rule.replaceWithSplitMark)
							parseStr = XRegExp.replace(parseStr,exp,'@');
						else
							parseStr = XRegExp.replace(parseStr,exp,rule.replaceWith);
					}
				}

				parseStr = parseStr.split('@')
			}

			let strings = [];

			if(parseStr.constructor === Array) {
				for(let i=0;i<parseStr.length;i++) {
					strings = [
						...strings,
						<span
							style={{lineHeight: '40px', display: 'block'}}
							key={i}
						>
							{parseStr[i]}
						</span>
					]
				}
			} else {
				strings = parseStr;
			}


			const element = (
				<Tab
					label={`${i+1}`}
					key={i}
				>
					<span style={{lineHeight: '40px'}}>{strings}</span>
				</Tab>
			);


			tabs = [...tabs, element];
		}

		const preview = (
			<div className="preview-background">
				<h4 style={{marginBottom: '20px'}}>Column Entries Preview</h4>
				<Tabs
					contentContainerStyle={{
						padding: '20px',
						marginBottom: '20px',
						background: 'rgba(247, 247, 247, 0.2)'
					}}
					inkBarStyle={{backgroundColor: 'white'}}
				>
					{tabs}
				</Tabs>
			</div>
		)

		return preview;
	}

	previewRules = () => {
		if(this.state.rulePreview) this.setState({ rulePreview: false })
			else this.setState({ rulePreview: true })
	}

	splitMultiValue = () => {
		const {
			multiValueIndex,
			ruleProfiles,
			activeRuleProfile
		} = this.props.multiValue;

		this.props.updateStateStore({ loading: true });
		// Split Multi Value
		setTimeout(() => {
			axios.post('/splitMultiValue', {
				entityIndex: multiValueIndex,
				replaceRegExps: ruleProfiles[activeRuleProfile].rules
			}).then(res => {
				this.props.updateMultiValueStore({ multiValueCountData: res.data.multiValueCountMap });
				this.props.increaseProgress(14.28);

				axios.post('/createOutputRecords', {
					entityIndex: multiValueIndex
				}).then(res => {
					console.log(`[AXONPARSE] Output records created: ${res.data.recordsCnt}`);
					this.props.increaseProgress(14.28);

					const parsedPath = `${this.props.originalPath}_PARSED`;
					axios.post('/exportRecords', {
						filePathOut: parsedPath,
						prependRowExpansionIdentifier: this.props.multiValue.prependRowExpansionIdentifier,
						prependRowExpansionIdentifierDelimiter: ',',
						prependRowExpansionIdentifierHeader: this.props.multiValue.prependRowExpansionIdentifierHeader,
					 }).then(res => {
						console.log(`[AXONPARSE] Records exported: ${res.data.recordsCnt}`);
						// Set parsed path on state to be downloaded
						axios.post('/download', { filePath: parsedPath }).then(res => {
							const blob = new Blob([res.data]);
							this.props.updateStateStore({
								loading: false,
								route: 'download'
							});

							this.props.increaseProgress(14.28);

							this.props.updateFileStore({ blobURL: URL.createObjectURL(blob), blob });
						}).catch(err => console.error(err));
					}).catch(err => console.error(err));
				}).catch(err => console.error(err));
			}).catch(err => console.error(err))
		}, 100);
	}
}



