import React, { Component } from 'react'

// Helpers
import axios from 'axios'

// Custom React components
import Button from 'components/Button'
import Rules from 'components/Rules'

// Third-party React components
import { Table, Column, Cell } from 'fixed-data-table-2'
import 'fixed-data-table-2/dist/fixed-data-table.css'

export default class SetMultiValue extends Component {
	constructor(props) {
		super(props);

		this.inputs = []
	}

	componentDidMount() {
		window.addEventListener('resize', this.updateTableWidth);

		this.props.updateTableStore({ tableWidth: document.getElementsByClassName('steps')[0].clientWidth - 40 });
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.updateTableWidth);
	}

	render() {
		return (
			<div className="setMultiValue">
				<div className="tableDataPreview">
					<Table
						rowHeight={50}
						rowsCount={this.props.table.recordCnt}
						width={this.props.table.tableWidth}
						maxHeight={800}
						headerHeight={50}
						onColumnResizeEndCallback={(w, i) => this.handleColumnResize(w, i)}
						isColumnResizing={false}
					>
						{this.renderTable()}
					</Table>
					{/*<Button
						onClick={this.incrementDisplayedRecords}
						>
						Add Another Record
					</Button>*/}
				</div>
			</div>
		)
	}

	renderTable = () => {
		const headers = this.props.table.headers;
		let table = [];

		if(headers.length > 0) {
			headers.map(header => {
				const index = headers.indexOf(header);

				let checked = false;

				if(this.props.activeMultiValue === index) checked = true;

				const headerElement = (
					<div className="tableHeader row middle-xs">
						<input
							type="checkbox"
							onChange={() => this.handleInputClick(index)}
							ref={c => this.inputs = [...this.inputs, c]}
							checked={checked}
						/>
						<span>{header}</span>
					</div>
				)

				const element = (
					<Column
						key={index}
						header={props => headerElement}
						cell={props => (
							<Cell {...props}>
								{this.props.table.data[props.rowIndex][index] ?
									this.props.table.data[props.rowIndex][index].replace(/"/g, '')
								: null}
							</Cell>
						)}
						width={this.props.table.columnWidths[index]}
						isResizable={true}
						columnKey={index}
						style={{color: 'black'}}
					/>
				);
				table = [...table, element];
			});
		}

		return table;
	}

	incrementDisplayedRecords = () => {
		let ids = [this.props.table.recordIds[this.props.table.displayedRecordCnt+1]];
		console.log(ids);
		axios.post('/getRecordsById', { ids }).then(res => {
			let entities = res.data.records[0].entities;

			this.props.updateTableStore({
				data: [...this.props.table.data, entities],
				displayedRecordCnt: this.props.table.displayedRecordCnt+1
			});
		}).catch(err => console.error(err));
	}

	handleColumnResize = (width, index) => {
		let { columnWidths } = this.props.table;

		columnWidths.splice(index, 1, width);

		this.props.updateTableStore({ columnWidths });

	}

	handleInputClick = (index) => {
		const value = this.props.activeMultiValue;
 		if(value === index) {
			this.props.updateMultiValueStore({ multiValueIndex: null });
		} else {
			this.props.updateMultiValueStore({ multiValueIndex: index });
		}
	}

	updateTableWidth = () => {
		let tableWidth = document.getElementsByClassName('steps')[0].clientWidth - 40;

		this.props.updateTableStore({ tableWidth });
	}

	handleRowClass = (index) => {
		if(index % 2 === 0) {
			return 'evenRow';
		} else {
			return 'oddRow';
		}
	}
}



