import React, { Component } from 'react'

// Helpers
import axios from 'axios'
import tingle from 'tingle.js'
import 'tingle.js/dist/tingle.min.css'

// Custom components
import Button from 'components/Button'

// Third-party components
import Dropzone from 'react-dropzone'
import Select from 'react-select'
import 'react-select/dist/react-select.css'

export default class Upload extends Component {
	constructor(props) {
		super(props)

		this.state = {
			recordDelimiter: '\n',
			recordDelimiterOptions: [
				{ value: '\n', label: 'New Line'},
				{ value: '|', label: 'Pipe (|)'},
				{ value: ',', label: 'Comma (,)'},
				{ value: '\t', label: 'Tab'}
			],
			hasHeader: true
		}
	}

	render() {
		return (
			<div>
				<label>Record Delimiter</label>
				<Select
					value={this.state.recordDelimiter}
					options={this.state.recordDelimiterOptions}
					onChange={val => this.handleDelimiterChange(null, null, val)}
					clearable={false}
					style={{marginTop: '5px', marginBottom: '20px'}}
				/>

				<label>
					Contains header row:
					<input
						name="hasHeader"
						type="checkbox"
						checked={this.state.hasHeader}
						onChange={(e) => this.handleCheckboxChange(e)} 
						style={{height: '16px'}}
						/>
				</label>

				<Dropzone
					onDrop={files => this.onDrop(files)}
					multiple={false}
					className="dropzone"
					ref={c => this._dropzone = c}
					style={{marginTop: '20px'}}
				>
					<p className="row middle-xs">Drag and drop your file in here, click on this box,<br /> or
					click the "Upload" button to upload a file.</p>
				</Dropzone>
			</div>
		)
	}

	componentDidMount() {
		this.props.updateFileStore({ dropzoneRef: this._dropzone });
	}

	onDrop = files => {
		let formData = new FormData();
		formData.append('fileUploaded', files[0]);
		if(window.FileReader && window.File && window.Blob) {
			axios.defaults.baseURL = this.props.state.host;

			// Show loading animation
			this.props.updateStateStore({ loading: true });

			// Start POST parsing chain
			axios.post('/upload', formData).then(res => {
				console.log(`[AXONPARSE] Upload completed.`);

				this.props.increaseProgress(14.28);

				// Store path for use in exportRecords POST
				this.props.updateFileStore({
					originalPath: res.data.path,
					newFileName: `${res.data.originalname.replace(/\.txt$/, '')}_PARSED.txt`
				});

				// Send returned path to importRecords for consumption
				axios.post('/importRecords', {
					filePathIn: res.data.path,
					deleteTemporaryFile: true,
					recordDelimiter: this.state.recordDelimiter,
					hasHeader: this.state.hasHeader
				}).then(res => {
					if(res.data.recordsCnt > 0) {
						console.log(`[AXONPARSE] Records successfully imported: ${res.data.recordsCnt}`);

						this.props.updateStateStore({
							progress: this.props.state.progress+14.28,
							loading: false,
							route: 'entrySplit'
						});
					} else {
						console.log(res.data.recordsCnt);
						// TODO: display error
						console.log('IMPORT ERROR!')

						var modal = new tingle.modal({
						    footer: false,
						    stickyFooter: false,
						    cssClass: ['error-msg']
						});

						// set content
						modal.setContent(`
							<h4>Import Error</h4>

							<p>
								The import process of your chosen file using the record delimiter set returned zero records. Please try a different delimiter or file.
							</p>
							`);

						// open modal
						modal.open();

						this.props.updateStateStore({ loading: false, progress: 0 });
					}
				}).catch(err => console.error(err));
			}).catch(err => {
				const modal1 = new tingle.modal({
					footer: false,
					stickyFooter: false,
					cssClass: ['error-msg']
				});

				modal1.setContent(`
					<h4>Upload Error</h4>

					<p><i>${err}</i></p>

					<p>Please double check to make sure you have the correct host by
					clicking on the cog icon next to AXONPARSE.</p>
				`);

				modal1.open();

				this.props.updateStateStore({ loading: false });
			});
		} else console.log('This browser does not support the FileReader/File/Blob APIs.');
	}

	handleDelimiterChange = (e, i, val) => {
		this.setState({ recordDelimiter: val.value });
	}

	handleCheckboxChange(e) {
		this.setState({ hasHeader: e.target.checked })
	}

	uploadButton = () => {
		if(this._dropzone !== null) {
			this._dropzone.open();
		}
	}
}



