export const UPDATE_STATE_STORE = 'parse/state/UPDATE_STORE';

if(localStorage.getItem('host') !== null) host = JSON.parse(localStorage.getItem('host'))
	else host = 'https://axonai.ai:8090'

const initialState = {
	progress: 0,
	loading: false,
	route: 'login',
	host
}

export default function reducer(state = initialState, action) {
	switch(action.type) {
		case UPDATE_STATE_STORE:
			let nextState = {
				...state,
				...action.payload
			}
			return nextState;
		default:
			return state;
	}
}

export function updateStateStore(payload, store = false) {
	if(store) {
		// if localStorage exists, store
		if (typeof(Storage) !== "undefined") {
			for(let key in payload) {
				if(payload.hasOwnProperty(key)) {
					localStorage.setItem(key, JSON.stringify(payload[key]));
				}
			}
		} else console.log('NO LOCAL STORAGE SUPPORT.')
	}

	return { type: UPDATE_STATE_STORE, payload }
}



