import React, { Component } from 'react'

// CSS
import 'css/index.styl'
import 'css/flexboxgrid.min.css'

// Redux
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import { updateStateStore } from 'modules/state'
import { updateFileStore } from 'modules/file'
import { updateFunctionsStore } from 'modules/functions'
import { updateEntrySplitStore } from 'modules/entrySplit'
import {
	updateMultiValueStore,
	addNewRuleProfile,
	addNewRule,
	deleteRule,
 	moveRule } from 'modules/multiValue'
import { updateTableStore } from 'modules/table'

// Helpers
import axios from 'axios'
import crypto from 'crypto'

// Custom React components
import Login from 'components/Login'
import Upload from 'components/Upload'
import Button from 'components/Button'
import EntrySplit from 'components/EntrySplit'
import SetMultiValue from 'components/SetMultiValue'
import CustomStepper from 'components/CustomStepper'
import Rules from 'components/Rules'
import Download from 'components/Download'

// Third-party React components
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider'
import getMuiTheme from 'material-ui/styles/getMuiTheme'
import CircularProgress from 'material-ui/CircularProgress'
import FaCog from 'react-icons/lib/fa/cog'

import injectTapEventPlugin from 'react-tap-event-plugin'

function mapStateToProps({ state, file, entrySplit, multiValue, table, functions }) {
	return { state, file, entrySplit, multiValue, table, functions }
}

function mapDispatchToProps(dispatch) {
	return bindActionCreators({
		updateStateStore,
		updateFileStore,
		updateFunctionsStore,
		updateEntrySplitStore,
		updateMultiValueStore,
		updateTableStore,
		addNewRuleProfile,
		addNewRule,
		deleteRule,
		moveRule
	}, dispatch);
}

@connect(mapStateToProps, mapDispatchToProps)
export default class App extends Component {
	constructor(props) {
		super(props);

		// Needed for onTouchTap
		// http://stackoverflow.com/a/34015469/988941
		injectTapEventPlugin();

		this.state = {
			inputShown: false
		}
	}

	render() {
		let content = null, currentStep = null, stepIndex = 0;

		switch(this.props.state.route) {
			case 'upload':
				currentStep = (
					<Upload
						increaseProgress={this.increaseProgress}
						updateStateStore={this.props.updateStateStore}
						updateFileStore={this.props.updateFileStore}
						state={this.props.state}
						increaseProgress={this.increaseProgress}
					/>
				)
				break;

			case 'entrySplit':
				stepIndex = 1
				currentStep = (
					<EntrySplit
						updateStateStore={this.props.updateStateStore}
						updateFileStore={this.props.updateFileStore}
						updateFunctionsStore={this.props.updateFunctionsStore}
						entrySplit={this.props.entrySplit}
						updateEntrySplitStore={this.props.updateEntrySplitStore}
						state={this.props.state}
						table={this.props.table}
						increaseProgress={this.increaseProgress}
						updateTableStore={this.props.updateTableStore}
					/>
				)
				break;

			case 'setMultiValue':
				stepIndex = 2
				currentStep = (
					<SetMultiValue
						table={this.props.table}
						activeMultiValue={this.props.multiValue.multiValueIndex}
						state={this.props.state}
						updateTableStore={this.props.updateTableStore}
						updateStateStore={this.props.updateStateStore}
						updateMultiValueStore={this.props.updateMultiValueStore}
						increaseProgress={this.increaseProgress}
					/>
				)
				break;

			case 'rules':
				stepIndex = 3
				currentStep = (
					<Rules
						multiValue={this.props.multiValue}
						updateFunctionsStore={this.props.updateFunctionsStore}
						updateMultiValueStore={this.props.updateMultiValueStore}
						updateStateStore={this.props.updateStateStore}
						updateFileStore={this.props.updateFileStore}
						table={this.props.table}
						state={this.props.state}
						increaseProgress={this.increaseProgress}
						addNewRuleProfile={this.props.addNewRuleProfile}
						addNewRule={this.props.addNewRule}
						deleteRule={this.props.deleteRule}
						moveRule={this.props.moveRule}
					/>
				)
				break;

			case 'download':
				stepIndex = 4;
				currentStep = (
					<Download
						file={this.props.file}
						uploadMore={this.uploadMore}
						data={this.props.multiValue.multiValueCountData}
					/>
				)
				break;
			default:
				currentStep = null;
		}

		if(this.props.state.route === 'login') {
			content = (
				<div className="row">
					<div className="col-xs-12">
						<Login updateStateStore={this.props.updateStateStore} />
					</div>
				</div>
			)
		} else {
			content = (
				<div className="row">
					<div className="col-xs-12 col-md-4">
						<CustomStepper
							stepIndex={stepIndex}
							state={this.props.state}
							dropzone={this.props.file.dropzoneRef}
							identifyEntries={this.props.functions.identifyEntries}
							splitMultiValue={this.props.functions.splitMultiValue}
							activeMultiValue={this.props.multiValue.multiValueIndex}
							updateStateStore={this.props.updateStateStore}
							updateMultiValueStore={this.props.updateMultiValueStore}
							increaseProgress={this.increaseProgress}
							originalPath={this.props.file.originalPath}
						/>
					</div>
					<div className="col-xs-12 col-md-8 steps">{currentStep}</div>
				</div>
			)
		}

		const muiTheme = getMuiTheme({
			primary1Color: '#292F36',
			accent1Color: 'black',
			stepper: {
				iconColor: '#292F36',
				textColor: 'white',
				disabledTextColor: 'rgba(255, 255, 255, .3)',
				inactiveIconColor: ''
			},
			tabs: {
				backgroundColor: '#292F36',
				textColor: 'rgba(255, 255, 255, .3)'
			}
		});

		return (
			<MuiThemeProvider muiTheme={muiTheme}>
				<div className="app-root tingle-content-wrapper" id="particles">
					<div className="row">
						<div className="col-xs-12 banner">
							<div
								className="progress"
								style={{width: `${this.props.state.progress}%`}}
							/>
						</div>
					</div>
					<div className="row">
						<div className="col-xs-1 col-md-2" />
						<div className="content col-xs-12">
							<div className="row">
								<div className="col-xs-12 header">
									<h1 className="mono" style={{display: 'inline-block'}}>AXON<span className="thin">PARSE</span></h1>
									<FaCog
										className="settings-cog"
										onClick={() => this.showHostInput()}
									/>
									{this.state.inputShown ?
										<div style={{display: 'inline-block', marginLeft: '20px'}}>
											Host<br/>
											<input
												value={this.props.state.host}
												onChange={(e) => this.handleHostChange(e)}
												style={{width: '220px'}}
												ref={c => this.hostInput = c}
											/>
										</div> : null}

									{this.props.state.loading ?
										<div style={{width: '0', height: '0', position: 'relative', float: 'right'}}>
											<div style={{position: 'absolute', right: '0px', top: '-15px'}}>
												<CircularProgress color="white" />
											</div>
										</div>
									: null}
								</div>
							</div>

							{content}

						</div>
						<div className="col-xs-1 col-md-2" />
					</div>
				</div>
			</MuiThemeProvider>
		)
	}

	uploadMore = () => {
		this.props.updateStateStore({ route: 'upload', progress: 0 });

		this.props.updateMultiValueStore({ multiValueIndex: null });
	}

	updateAppState = newState => {
		this.setState({ ...this.state, ...newState });
	}

	increaseProgress = amount => {
		const { progress } = this.props.state;
		// ^ is the same as: const progress = this.state.progress;

		if(progress !== 100) {
			this.props.updateStateStore({ progress: progress+amount });
		} else console.log('Progress is already at 100..');
	}

	showHostInput = () => {
		if(this.state.inputShown) this.setState({ inputShown: false });
			else {
				this.setState({ inputShown: true }, () => {
					console.log('done');
				});

				setTimeout(() => {
					this.hostInput.focus();
				}, 1);
			}
	}

	handleHostChange = (e) => {
		this.props.updateStateStore({ host: e.target.value }, true)
	}

	ieDownload = () => {
		console.log(this.state.blob, this.state.newFileName);
		let didItWork = window.navigator.msSaveOrOpenBlob(this.state.blob, this.state.newFileName);
		console.log(didItWork)
	}
}



