export const UPDATE_MULTIVALUE_STORE = 'parse/multiValue/UPDATE_STORE'
export const ADD_NEW_PARSE_RULES_PROFILE = 'parse/multiValue/ADD_NEW_PARSE_RULES_PROFILE'
export const ADD_NEW_RULE = 'parse/multiValue/ADD_NEW_RULE'
export const DELETE_RULE = 'parse/multiValue/DELETE_RULE'
export const MOVE_RULE = 'parse/multiValue/MOVE_RULE'

let ruleProfiles = [
	{
		label: 'Default',
		rules: [
			{
				name:'Remove Fractions',
				apply:true,
				regExp:'[0-9]\\\/[0-9]',
				regExpApplication:'g',
				replaceWithSplitMark:false,
				replaceWith:''
			},
			{
				name:'Remove Leftover Numbers',
				apply:true,
				regExp:'\\\d',
				regExpApplication:'g',
				replaceWithSplitMark:false,
				replaceWith:''
			},
			{
				name:'Remove everything between Parentheses',
				apply:true,
				regExp:'\\\([^)]*\\\)',
				regExpApplication:'g',
				replaceWithSplitMark:false,
				replaceWith:''
			},
			{
				name:'Split @ Ampersand',
				apply:true,
				regExp:'&',
				regExpApplication:'g',
				replaceWithSplitMark:true
			},
			{
				name:'Split @ "AND" & "and"',
				apply:true,
				regExp:' AND ',
				regExpApplication:'gi',
				replaceWithSplitMark:true
			},
			{
				name:'Split @ " , "',
				apply:true,
				regExp:' , ',
				regExpApplication:'g',
				replaceWithSplitMark:true
			},
			{
				name:'Discard any extraneous "."',
				apply:true,
				regExp:' \\\.',
				regExpApplication:'g',
				replaceWithSplitMark:false,
				replaceWith:''
			},
			{
				name:'Discard any Double Quotes',
				apply:true,
				regExp:'"',
				regExpApplication:'g',
				replaceWithSplitMark:false,
				replaceWith:''
			},
			{
				name:'Split @ "%"',
				apply:true,
				regExp:'%',
				regExpApplication:'g',
				replaceWithSplitMark:true
			},
			{
				name:'Split @ "ETAL"',
				apply:true,
				regExp:'ETAL',
				regExpApplication:'g',
				replaceWithSplitMark:true
			}
		]
	},
];

if(localStorage.getItem('ruleProfiles') !== null)
	ruleProfiles = JSON.parse(localStorage.getItem('ruleProfiles'));

const initialState = {
	multiValueIndex: null,
	expandedRules: [],
	ruleProfiles,
	activeRuleProfile: 0,
	prependRowExpansionIdentifier:false,
	prependRowExpansionIdentifierDelimiter:',',
	prependRowExpansionIdentifierHeader:'RowExpansionIdentifier',
	multiValueCountData: null
}

export default function reducer(state = initialState, action) {
	switch(action.type) {
		case UPDATE_MULTIVALUE_STORE:
			let nextState = {
				...state,
				...action.payload
			}
			return nextState;

		case ADD_NEW_PARSE_RULES_PROFILE:
			let nextState0 = {
				...state,
				ruleProfiles: [
					...state.ruleProfiles,
					{
						label: action.name,
						rules: [
							...state.ruleProfiles[state.activeRuleProfile].rules
						]
					}
				]
			}

			localStorage.setItem('ruleProfiles', JSON.stringify(nextState0.ruleProfiles));
			return nextState0;

		case ADD_NEW_RULE:
			let profiles = state.ruleProfiles,
				activeProfile = profiles[state.activeRuleProfile];

			activeProfile.rules.push({
				name: action.name,
				apply:true,
				regExp:'',
				regExpApplication:'',
				replaceWithSplitMark:false,
				replaceWith:''
			});

			profiles.splice(state.activeRuleProfile, 1, activeProfile);

			let nextState1 = {
				...state,
				ruleProfiles: profiles
			}

			localStorage.setItem('ruleProfiles', JSON.stringify(nextState1.ruleProfiles));
			return nextState1;

		case DELETE_RULE:
			let profiles0 = state.ruleProfiles,
				activeProfile0 = profiles0[state.activeRuleProfile];

			activeProfile0.rules.splice(action.index, 1);

			let nextState2 = {
				...state,
				ruleProfiles: profiles0
			}

			localStorage.setItem('ruleProfiles', JSON.stringify(nextState2.ruleProfiles));
			return nextState2;

		case MOVE_RULE:
			let profiles1 = state.ruleProfiles,
				activeProfile1 = profiles1[state.activeRuleProfile];

			const ruleToMove = activeProfile1.rules.splice(action.from, 1);

			//if first in array is decremented, push to end of array
			if(action.from === 0 && action.to === -1) {
				activeProfile1.rules.push(...ruleToMove);
			// if last in array and is incremented, add to beginning of array
			} else if(action.from === activeProfile1.rules.length && action.to === activeProfile1.rules.length+1) {
				activeProfile1.rules.unshift(...ruleToMove);
			} else {
				activeProfile1.rules.splice(action.to, 0, ...ruleToMove);
			}

			let nextState3 = {
				...state,
				ruleProfiles: profiles1
			}

			localStorage.setItem('ruleProfiles', JSON.stringify(nextState3.ruleProfiles));
			return nextState3;

		default:
			return state;
	}
}

export function updateMultiValueStore(payload, store = false) {
	if(store) {
		// if localStorage exists, store
		if (typeof(Storage) !== "undefined") {
			for(let key in payload) {
				if(payload.hasOwnProperty(key)) {
					localStorage.setItem(key, JSON.stringify(payload[key]));
				}
			}
		} else console.log('NO LOCAL STORAGE SUPPORT.')
	}

	return { type: UPDATE_MULTIVALUE_STORE, payload }
}

export function addNewRuleProfile(name) {
	return { type: ADD_NEW_PARSE_RULES_PROFILE, name }
}

export function addNewRule(name) {
	return { type: ADD_NEW_RULE, name }
}

export function deleteRule(index) {
	return { type: DELETE_RULE, index }
}

export function moveRule(from, to) {
	return { type: MOVE_RULE, from, to }
}



