export const UPDATE_FUNCTIONS_STORE = 'parse/functions/UPDATE_STORE';

const initialState = {
	identifyEntries: null,
	splitMultiValue: null
}

export default function reducer(state = initialState, action) {
	switch(action.type) {
		case UPDATE_FUNCTIONS_STORE:
			let nextState = {
				...state,
				...action.payload
			}
			return nextState;
		default:
			return state;
	}
}

export function updateFunctionsStore(payload, store = false) {
	if(store) {
		// if localStorage exists, store
		if (typeof(Storage) !== "undefined") {
			for(let key in payload) {
				if(payload.hasOwnProperty(key)) {
					localStorage.setItem(key, JSON.stringify(payload[key]));
				}
			}
		} else console.log('NO LOCAL STORAGE SUPPORT.')
	}

	return { type: UPDATE_FUNCTIONS_STORE, payload }
}



