export const UPDATE_FILE_STORE = 'parse/file/UPDATE_STORE';

const initialState = {
	headers: [],
	data: [],
	columnWidths: [],
	recordCnt: 8,
	tableWidth: 500,
	displayedRecordCnt: 0,
	recordIds: []
}

export default function reducer(state = initialState, action) {
	switch(action.type) {
		case UPDATE_FILE_STORE:
			let nextState = {
				...state,
				...action.payload
			}
			return nextState;
		default:
			return state;
	}
}

export function updateTableStore(payload, store = false) {
	if(store) {
		// if localStorage exists, store
		if (typeof(Storage) !== "undefined") {
			for(let key in payload) {
				if(payload.hasOwnProperty(key)) {
					localStorage.setItem(key, JSON.stringify(payload[key]));
				}
			}
		} else console.log('NO LOCAL STORAGE SUPPORT.')
	}

	return { type: UPDATE_FILE_STORE, payload }
}



