import React, { Component } from 'react'

// CSS
import styles from 'css/'

// Custom components
import Button from 'components/Button'

// Helpers
import axios from 'axios'
import XRegExp from 'xregexp'

import MdCheck from 'react-icons/lib/md/check'
import MdDelete from 'react-icons/lib/md/delete'
import Select from 'react-select'

export default class SetParams extends Component {
	constructor(props) {
		super(props);

		this.state = {
			recordPreview: '',
			showAddInput: false,
			newProfileName: ''
		}

		this.timeout = null;
	}

	componentWillMount() {
		axios.get('/getRecordIDs').then(res => {
			axios.post('/getRecordsById', { ids: [res.data.ids[0]]}).then(res => {
				const record = res.data.records[0].rawRecord.line;

				console.log(`[AXONPARSE] First record retrieved successfully for preview.`);
				this.setState({ recordPreview: record });
			}).catch(err => console.error(err));
		}).catch(err => console.error(err));
	}

	render() {
		let matchedPreview = null, isValid = true, regEx;

		const {
			entrySplitProfiles,
			activeProfile
		} = this.props.entrySplit;

		const activeRegExp = entrySplitProfiles[activeProfile].regExp;

		try {
			regEx = new RegExp(activeRegExp, 'g')
		} catch(e) {
			isValid = false;
			matchedPreview = (
				<div className="matchedPreview">
					<h4>Record Preview</h4>
					<p>{this.state.recordPreview}</p>
				</div>
			)
		}

		if(isValid) {
			const replacedDOMString = this.state.recordPreview.replace(regEx, match => {
				return `<span class="regexPreview">${match}</span>`;
			});

			matchedPreview = (
				<div className="matchedPreview">
					<h4>Record Preview</h4>
					<p dangerouslySetInnerHTML={{__html: replacedDOMString }} />
				</div>
			);
		}

		let selectOptions = [];

		const availableProfiles = entrySplitProfiles.map(profile => {
			return { label: profile.label, value: entrySplitProfiles.indexOf(profile) }
		});

		return (
			<div className="entry-split">
				<h4>Entry Split Profiles</h4>
				{this.addButton()}
				{this.deleteButton()}
				<p style={{lineHeight: '45px', marginLeft: '10px', display: 'inline-block'}}>{this.state.statusMsg}</p>
				{this.state.showAddInput ?
					<form
						onSubmit={(e) => this.handleProfileAdd(e)}
					>
						<input
							value={this.state.newProfileName}
							placeholder="Profile Name"
							onChange={(e) => this.handleProfileNameChange(e)}
							style={{margin: '0 0 10px 0'}}
							ref={c => this.input = c}
						/>
					</form> : null}
				<label style={{marginTop: '20px'}}>Active Profile</label>
				<Select
					options={availableProfiles}
					value={entrySplitProfiles[activeProfile]}
					style={{margin: '5px 0 40px 0'}}
					onChange={(val) => this.handleProfileChange(val)}
					clearable={false}
				/>
				<label htmlFor="entries">Entry Split Regular Expression</label>
				<input
					id="entries"
					value={activeRegExp}
					onChange={e => this.onChange(e)}
					className="password"
					style={{marginBottom: '30px'}}
				/>

				{matchedPreview}
			</div>
		)
	}

	componentDidMount() {
		this.props.updateFunctionsStore({ identifyEntries: this.identifyEntries });
	}

	handleProfileChange = (val) => {
		this.props.updateEntrySplitStore({ activeProfile: val.value });
	}

	handleProfileNameChange = (e) => {
		this.setState({ newProfileName: e.target.value });
	}

	addButton = () => {
		let text;

		if(!this.state.showAddInput) {
			// show input
			text = '+'
		} else {
			if(this.state.newProfileName.length > 0) {
				// add new profile
				text = <MdCheck />
			} else {
				// hide input
				text = '-'
			}
		}

		return (
			<button
				onClick={() => this.handleProfileAdd()}
				style={{marginBottom: '10px', width: '100px'}}
			>
				{text}
			</button>
		)
	}

	deleteButton = () => {
		return (
			<button
				style={{marginLeft: '10px'}}
				onClick={() => this.handleProfileDelete()}
			>
				<MdDelete />
			</button>
		)
	}

	handleProfileDelete = () => {
		const {
			activeProfile,
			entrySplitProfiles
		} = this.props.entrySplit;

		if(activeProfile === 0) {
			this.setState({ statusMsg: 'You cannot delete the default entry split profile.'});

			setTimeout(() => {
				this.setState({ statusMsg: null });
			}, 5000);
		} else {
			entrySplitProfiles.splice(activeProfile, 1);

			this.props.updateEntrySplitStore({ entrySplitProfiles, activeProfile: 0 }, true);
		}
	}

	handleProfileAdd = (e) => {
		const {
			activeProfile,
			entrySplitProfiles
		} = this.props.entrySplit;

		if(e) e.preventDefault();

		if(!this.state.showAddInput) {
			this.setState({ showAddInput: true });

			setTimeout(() => {
				this.input.focus();
			}, 50);
		} else {
			if(this.state.newProfileName.length > 0 ) {
				const newProfiles = [
					...entrySplitProfiles,
					{
						label: this.state.newProfileName,
						regExp: '(".*?"|[^",\\\s]+)(?=\\\s*,|\\\s*$)'
					}
				];

				this.props.updateEntrySplitStore({
					entrySplitProfiles: newProfiles,
					activeProfile: newProfiles.length-1
				}, true);

				this.setState({
					...this.state,
					statusMsg: `${this.state.newProfileName} entry split profile created.`,
					showAddInput: false,
					newProfileName: ''
				});

				setTimeout(() => {
					this.setState({ statusMsg: null });
				}, 5000);
			} else {
				if(e) {
					this.setState({ statusMsg: 'You must provide a name in order to create a new entry split profile.' });

					setTimeout(() => {
						this.setState({ statusMsg: null });
					}, 5000);
				} else this.setState({ showAddInput: false });
			}
		}
	}

	onChange = e => {
		const {
			entrySplitProfiles,
			activeProfile
		} = this.props.entrySplit;

		if(activeProfile !== 0) {
			entrySplitProfiles[activeProfile].regExp = e.target.value;

			this.props.updateEntrySplitStore({ entrySplitProfiles }, true);
		} else {
			this.setState({ statusMsg: 'You cannot edit the default profile. Please create a new one.' });

			if(this.timeout !== null) {
				clearTimeout(this.timeout);
			}

			this.timeout = setTimeout(() => {
				this.setState({ statusMsg: null });
			}, 5000);
		}
	}

	identifyEntries = () => {
		const {
			entrySplitProfiles,
			activeProfile
		} = this.props.entrySplit;

		this.props.updateStateStore({ loading: true });

		const activeRegExp = entrySplitProfiles[activeProfile].regExp

		// Identify Entries
		axios.post('/identifyEntries', {
			entrySplitRegExp: activeRegExp
		}).then(res => {
			console.log(`[AXONPARSE] Entries identified: ${res.data.recordsCnt}`);
			axios.post('/getRecordIDs', { headerOnly: false, excludeHeader: false }).then(res => {
				let ids = [];

				for(let i = 0; i < 19; i++) ids = [...ids, res.data.ids[i], "R0"];

				axios.post('/getRecordsById', { ids }).then(res => {
					// Parse records into separate arrays of strings
					const records = res.data.records;

					let tableData = [],
						headers = [],
						columnWidths = [];

					for(let i = 0; i < this.props.table.recordCnt+1; i++) {
						const record = records[i];

						if(record.rawRecord.isHeader) {
							headers = record.entities;

							headers.map(header => {
								columnWidths = [...columnWidths, header.length*13]
							});
						} else {
							let entries = record.entities;
							this.props.updateTableStore({ displayedRecordCnt: this.props.table.displayedRecordCnt+1 });
							tableData = [...tableData, entries];
						}
					}

					this.props.updateTableStore({
						headers,
						data: tableData,
						columnWidths,
						recordIds: ids
					});

					this.props.increaseProgress(14.28);

					this.props.updateStateStore({ route: 'setMultiValue', loading: false });
				});
			}).catch(err => console.error(err));
		}).catch(err => console.error(err));
	}
}



