export const UPDATE_ENTRYSPLIT_STORE = 'parse/entrySplit/UPDATE_STORE';

let profiles = [
	{
		label: 'Default',
		regExp:'(".*?"|[^",\\\s]+)(?=\\\s*,|\\\s*$)'
	}
]

if(localStorage.getItem('entrySplitProfiles') !== null) {
	profiles = JSON.parse(localStorage.getItem('entrySplitProfiles'));
}

const initialState = {
	entrySplitProfiles: profiles,
	activeProfile: 0
}


export default function reducer(state = initialState, action) {
	switch(action.type) {
		case UPDATE_ENTRYSPLIT_STORE:
			let nextState = {
				...state,
				...action.payload
			}
			return nextState;
		default:
			return state;
	}
}

export function updateEntrySplitStore(payload, store = false) {
	if(store) {
		// if localStorage exists, store
		if (typeof(Storage) !== "undefined") {
			for(let key in payload) {
				if(payload.hasOwnProperty(key)) {
					localStorage.setItem(key, JSON.stringify(payload[key]));
				}
			}
		} else console.log('NO LOCAL STORAGE SUPPORT.')
	}

	return { type: UPDATE_ENTRYSPLIT_STORE, payload }
}



