import React, { Component } from 'react'

// Helpers
import crypto from 'crypto'

// Custom components
import Button from 'components/Button'

export default class Login extends Component {
	constructor(props) {
		super(props)

		this.state = {
			correctPw: crypto.createHmac('sha256', 'Pars1ngRules!').digest('hex'),
			currentPwValue: '',
			hashedPw: {},
			errMsg: ''
		}
	}

	componentDidMount() {
		if(this.input) this.input.focus();
	}

	render() {
		return (
			<div>
				<p>Welcome to AXON Parse! Please login with the password
				provided to you by AxonAI.</p>
				<form onSubmit={(e) => this.tryLogin(e)}>
					<input
						type="password"
						name="login"
						value={this.state.currentPwValue}
						onChange={e => this.updatePw(e)}
						className="password"
						ref={c => this.input = c}
					/>
				</form>
				<Button
					onClick={() => this.tryLogin()}
					centered
					style={{marginTop: '40px'}}
				>
					Login
				</Button>
				{this.state.errMsg ?
					<p style={{marginTop: '40px'}}>{this.state.errMsg}</p> : null}
			</div>
		)
	}

	updatePw = e => {
		this.setState({
			...this.state,
			currentPwValue: e.target.value,
			hashedPw: crypto.createHmac('sha256', e.target.value)
		 });
	}

	tryLogin = (e) => {
		if(e) e.preventDefault();
		if(this.state.hashedPw.digest('hex') === this.state.correctPw) this.props.updateStateStore({ route: 'upload' });
			else this.setState({ errMsg: 'Password incorrect. Try again.' });
	}
}



