import React, { Component } from 'react'

import FaCaretDown from 'react-icons/lib/fa/caret-down'
import FaCaretUp from 'react-icons/lib/fa/caret-up'
import MdDelete from 'react-icons/lib/md/delete'
import MdCheck from 'react-icons/lib/md/check'
import MdRemoveRedEye from 'react-icons/lib/md/remove-red-eye'
import Select from 'react-select'

export default class RuleProfiles extends Component {
	constructor(props) {
		super(props)

		this.state = {
			profileName: '',
			newRuleName: '',
			showAddInput: false,
			showRuleNameInput: false,
			statusMsg: null,
			newRuleStatusMsg: null
		}

		this.timeout = null;
	}

	render() {
		const {
			ruleProfiles,
			activeRuleProfile
		} = this.props;

		const availableProfiles = ruleProfiles.map(profile => {
			return { label: profile.label, value: ruleProfiles.indexOf(profile) }
		});

		return (
			<div>
				<h5>Rule Profiles</h5>
				<div
					className="row"
					style={{margin: '20px 0'}}
				>
					{this.addButton()}
					<button
						onClick={() => this.deleteRuleProfile()}
						style={{marginRight: '10px'}}
					>
						<MdDelete />
					</button>
					<button
						onClick={() => this.props.previewRules()}
						className={this.props.rulePreview ? 'preview-active' : null}
					>
						<MdRemoveRedEye />
					</button>
					<p style={{lineHeight: '45px', marginLeft: '10px'}}>{this.state.statusMsg}</p>
				</div>
				<form onSubmit={(e) => this.handleFormSubmit(e)}>
					<input
						onChange={(e) => this.setState({ profileName: e.target.value })}
						value={this.state.profileName}
						className={this.state.showAddInput ? 'addProfile shown' : 'addProfile hidden'}
						placeholder="Profile Name"
						ref={c => this.input = c}
					/>
				</form>
				<label>Active Profile</label>
				<Select
					name="rule-profiles"
					options={availableProfiles}
					value={ruleProfiles[activeRuleProfile]}
					onChange={(val) => this.handleProfileChange(val)}
					clearable={false}
					style={{margin: '5px 0 30px 0'}}
				/>
				{this.renderRules()}
				{this.addNewRuleButton()}
				<form
					onSubmit={(e) => this.createNewRule(e)}
				>
					<input
						value={this.state.newRuleName}
						onChange={(e) => this.handleNewRuleNameChange(e)}
						placeholder="New Rule Name"
						className={this.state.showRuleNameInput ? 'addProfile shown' : 'addProfile hidden'}
						ref={c => this.ruleNameInput = c}
					/>
				</form>

			</div>
		)
	}

	addNewRuleButton = () => {
		let button;

		if(!this.state.showRuleNameInput) button = '+';
			else {
				if(this.state.newRuleName.length > 0) {
					button = <MdCheck />
				} else {
					button = '-'
				}
			}

		return (
			<div className="row" style={{margin: '10px 0'}}>
				<button
					onClick={() => this.createNewRule()}
					style={{margin: '5px 0', width: '100px'}}
				>
					{button}
				</button>
				<p style={{lineHeight: '55px', marginLeft: '10px'}}>{this.state.newRuleStatusMsg}</p>
			</div>

		)
	}

	addButton = () => {
		let button;
		if(!this.state.showAddInput) {
			// show add button
			button = '+'
		} else {
			if(this.state.profileName.length > 0) {
				// show save button
				button = <MdCheck />
			} else {
				// show minimize button
				button = '-'
			}
		}

		return (
			<button
				onClick={() => this.handleProfileAddButton()}
				style={{marginRight: '10px', width: '100px'}}
			>
				{button}
			</button>
		)

	}

	createNewRule = (e) => {
		if(e) e.preventDefault();

		if(!this.state.showRuleNameInput) {
			if(this.props.activeRuleProfile !== 0) {
				this.setState({ showRuleNameInput: true })

				setTimeout(() => {
					this.ruleNameInput.focus()
				}, 50);
			} else {
				this.setState({ statusMsg: 'You cannot add rules to the default profile. Please create a new one.'});

				if(this.timeout !== null) {
					clearTimeout(this.timeout);
				}

				this.timeout = this.clearStatus();
			}
		} else {
			if(this.state.newRuleName.length > 0) {
				// create new rule
				this.props.addNewRule(this.state.newRuleName);

				this.setState({
					...this.state,
					newRuleStatusMsg: `${this.state.newRuleName} rule successfully created.`,
					showRuleNameInput: false,
					newRuleName: ''
				});

				this.handleRuleExpand(this.props.ruleProfiles[this.props.activeRuleProfile].rules.length-1);

				setTimeout(() => {
					this.setState({ newRuleStatusMsg: null });
				}, 5000);
			} else {
				if(e) {
					this.setState({ newRuleStatusMsg: 'You must supply a name in order to create a new rule.' });

					setTimeout(() => {
						this.setState({ newRuleStatusMsg: null });
					}, 5000);
				} else {
					this.setState({ showRuleNameInput: false })
				}

			}
		}
	}

	handleNewRuleNameChange = (e) => {
		this.setState({ newRuleName: e.target.value })
	}

	handleProfileAddButton = () => {
		if(!this.state.showAddInput) {
			this.setState({ showAddInput: true });

			setTimeout(() => {
				this.input.focus()
			}, 50);
		} else {
			if(this.state.profileName.length > 0) {
				// save new profile with name
				this.props.addNewRuleProfile(this.state.profileName);

				this.selectNewestProfile();

				this.setState({
					...this.state,
					statusMsg: `${this.input.value} profile successfully created.`,
					showAddInput: false,
					profileName: ''
				});

				this.clearStatus();
			} else {
				// minimize
				this.setState({ showAddInput: false });
			}
		}
	}

	handleFormSubmit = (e) => {
		e.preventDefault();

		if(this.state.profileName.length > 0) {
			this.props.addNewRuleProfile(this.input.value);

			this.selectNewestProfile();

			this.setState({
				...this.state,
				statusMsg: `${this.input.value} profile successfully created.`,
				showAddInput: false,
				profileName: ''
			});

			this.clearStatus();
		} else {
			this.setState({ statusMsg: 'You must include a name with your new profile.'});

			this.clearStatus();
		}
	}

	selectNewestProfile = () => {
		this.props.updateMultiValueStore({ activeRuleProfile: this.props.ruleProfiles.length });
	}

	renderRules = () => {
		const {
			activeRuleProfile,
			ruleProfiles,
			expandedRules
		} = this.props;

		let rules = [];

		let activeProfile = ruleProfiles[activeRuleProfile];

		for(let i = 0; i < activeProfile.rules.length; i++) {
			const rule = activeProfile.rules[i],
			expanded = expandedRules.indexOf(i) > -1,

			element = (
				<div
					key={i}
					className="rule"
					style={{width: '93%'}}
				>
					<div>
						<input
							type="checkbox"
							checked={rule.apply}
							onChange={() => this.handleRuleChange(i, 'apply')}
							className="rule-expand"
							style={{marginTop: '10px'}}
						/>
						<p
							onClick={() => this.handleRuleExpand(i)}
							className="rule-expand"
							style={{maxWidth: '70%', lineHeight: '1.1rem', padding: '10px 0'}}
						>
							{`${i+1}. ${rule.name}`}
						</p>

						{activeRuleProfile !== 0 ?
							<div style={{float: 'right'}}>
								<MdDelete
									className="caret"
									style={{marginLeft: '10px'}}
									onClick={() => this.props.deleteRule(i)}
								>
									Delete
								</MdDelete>
								<FaCaretDown
									onClick={() => this.props.moveRule(i, i+1)}
									className="caret"
									style={{marginLeft: '10px'}}
								/>
								<FaCaretUp
									onClick={() => this.props.moveRule(i, i-1)}
									className="caret"
									style={{marginLeft: '10px'}}
								/>
							</div>
						: null}


					</div>
					{expanded ?
						<div className={!rule.apply ? 'expanded-rule disabled' : 'expanded-rule'}>
							<div className="individual-rule">
								<div className="label"><label htmlFor="exp">Expression</label></div>
								<input
									id="exp"
									value={rule.regExp}
									onChange={(e) => this.handleRuleChange(i, 'regExp', e)}
								/>
							</div>

							<div className="individual-rule">
								<div className="label"><label htmlFor="application">Apply</label></div>
								<input
									id="application"
									value={rule.regExpApplication}
									onChange={(e) => this.handleRuleChange(i, 'regExpApplication', e)}
								/>
							</div>

							<div className="individual-rule">
								<div className="label"><label htmlFor="split">Split After Removal</label></div>
								<input
									type="checkbox"
									id="split"
									checked={rule.replaceWithSplitMark}
									onChange={() => this.handleRuleChange(i, 'replaceWithSplitMark')}
								/>
							</div>

							{rule.hasOwnProperty('replaceWith') ?
								<div className="individual-rule">
									<div className="label"><label htmlFor="replace">Replace With</label></div>
									<input
										id="replace"
										value={rule.replaceWith}
										onChange={(e) => this.handleRuleChange(i, 'replaceWith', e)}
									/>
								</div>
							: null }
						</div>
					: null}
				</div>
			)

			rules = [...rules, element];
		}

		// split array in half
		const half = Math.ceil(rules.length / 2),
			leftRules = rules.splice(0, half);

		let rulesElement = (
			<div className="row">
				<div className="col-xs-12 col-sm-6">
					{leftRules}
				</div>
				<div className="col-xs-12 col-sm-6">
					{rules}
				</div>
			</div>
		)

		return rulesElement;
	}

	clearStatus = () => {
		setTimeout(() => {
			this.setState({ statusMsg: null });
		}, 5000);
	}

	handleProfileChange = (val) => {
		this.props.updateMultiValueStore({ activeRuleProfile: val.value });
	}

	handleRuleChange = (ruleIndex, key, e) => {
		const {
			ruleProfiles,
			activeRuleProfile
		} = this.props;

		let rules = ruleProfiles[activeRuleProfile].rules;

		// handle 'apply' rule change
		if(key === 'apply') {
			if(rules[ruleIndex].apply) {
				rules[ruleIndex] = {
					...rules[ruleIndex],
					apply: false
				}
			} else {
				rules[ruleIndex] = {
					...rules[ruleIndex],
					apply: true
				}
			}
		}

		// handle regExp rule change
		if(key === 'regExp') {
			rules[ruleIndex] = {
				...rules[ruleIndex],
				regExp: e.target.value
			}
		}

		// handle application change
		if(key === 'regExpApplication') {
			rules[ruleIndex] = {
				...rules[ruleIndex],
				regExpApplication: e.target.value
			}
		}

		// handle replaceWith change
		if(key === 'replaceWith') {
			rules[ruleIndex] = {
				...rules[ruleIndex],
				replaceWith: e.target.value
			}
		}

		// handle replaceWithSplitMark change
		if(key === 'replaceWithSplitMark') {
			if(rules[ruleIndex].replaceWithSplitMark) {
				rules[ruleIndex] = {
					...rules[ruleIndex],
					replaceWithSplitMark: false
				}
			} else {
				rules[ruleIndex] = {
					...rules[ruleIndex],
					replaceWithSplitMark: true
				}
			}
		}

		ruleProfiles[activeRuleProfile].rules = rules;

		this.props.updateMultiValueStore({ ruleProfiles }, true);
	}

	handleRuleExpand = (index) => {
		// if rule is not expanded, expand
		if(!(this.props.expandedRules.indexOf(index) > -1)) {
			this.props.updateMultiValueStore({ expandedRules: [ ...this.props.expandedRules, index ] });
		} else { 		// if rule is expanded, collapse
			// get index of expandedRule
			let expandedRules = this.props.expandedRules,
				i = expandedRules.indexOf(index);

			// remove that index
			expandedRules.splice(i, 1);

			this.props.updateMultiValueStore({ expandedRules });
		}
	}

	deleteRuleProfile = () => {
		const {
			activeRuleProfile,
			ruleProfiles
		} = this.props;

		if(activeRuleProfile !== 0) {
			ruleProfiles.splice(activeRuleProfile, 1);
		} else {
			this.setState({ statusMsg: 'You cannot delete the default rule profile.'});

			this.clearStatus();
		}

		this.props.updateMultiValueStore({ ruleProfiles, activeRuleProfile: 0 }, true);
	}
}



